import { Component, OnInit } from '@angular/core';
import { AlertController, IonicPage, LoadingController, NavController, NavParams, MenuController } from 'ionic-angular';
import { HomePage } from '../home/home';
import { AuthProvider } from "../../providers/auth/auth";
import { RegisterPage } from "../register/register";
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ProfileProvider } from '../../providers/profile/profile';
import { Platform } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  rForm: FormGroup;
  registrationForm: FormGroup;
  forgotForm: FormGroup;
  email: string;
  password: string;
  loginPage: any = false;
  forgotPage: any = true;
  registerPage: any = true;
  confirmPassword;


  constructor(public nav: NavController,
    public navParams: NavParams,
    public authService: AuthProvider,
    public alertCtrl: AlertController,
    public loadingCtrl: LoadingController,
    private fb: FormBuilder,
    public menu: MenuController,
    public profile: ProfileProvider,
    public platform: Platform) {

    this.rForm = fb.group({
      'email': [null, Validators.email],
      'password': [null, Validators.required],
      'validate': ''
    });

    this.registrationForm = fb.group({
      'email': [null, Validators.email],
      'password': [null, Validators.required],
      'confirmPassword': [null, Validators.required]
    })

    this.forgotForm = fb.group({
      'email': [null, Validators.email]
    })

    this.menu.enable(false);
    this.loginPage = false;
    this.forgotPage = true;
    this.registerPage = true;

  }

  showForgot() {
    this.loginPage = true;
    this.forgotPage = false;
  }

  showLogin() {
    this.loginPage = !this.loginPage;
    this.forgotPage = !this.forgotPage;
  }

  showRegister() {
    this.loginPage = !this.loginPage;
    this.registerPage = !this.registerPage;
  }

  skip() {
    this.nav.setRoot(HomePage);
  }
  login() {

    if (!this.email || !this.password) {
      let alert = this.alertCtrl.create({
        message: 'Please provide email and password',
        buttons: ['OK']
      });
      return alert.present();
    }

    
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });
    loading.present();

    this.authService.login(this.email, this.password).then(authData => {
      /* if (this.isCustomer(authData.uid))
 
       {
           localStorage.setItem('uid', authData.uid)
           loading.dismiss();
           this.nav.setRoot(HomePage);
       }
       else {
           loading.dismiss();
           let alert = this.alertCtrl.create({
               message: "EmailId already registered as a Cosmetologist",
               buttons: ['OK']
           });
           alert.present();
       }
       localStorage.setItem('uid', authData.uid)
       loading.dismiss();
 
       this.nav.setRoot(HomePage);*/

      this.profile.getProfileData(authData.uid).subscribe(res => {
        if (res.length > 0) {
          localStorage.setItem('email', this.email); //autologin
          localStorage.setItem('password', this.password);  //autologin
          localStorage.setItem('uid', authData.uid)
          loading.dismiss();
          this.nav.setRoot(HomePage);

        } else {  // you can write the logic here to use same emailid/password for client and partner
          loading.dismiss();
          let alert = this.alertCtrl.create({
            message: "EmailId already registered as a Cosmetologist",
            buttons: ['OK']
          });
          alert.present();
        }
      }, err => {
        console.log(JSON.stringify(err))
      }
      )

    }, error => {
      loading.dismiss();
      let alert = this.alertCtrl.create({
        message: error.message,
        buttons: ['OK']
      });
      alert.present();
    });
    //this.nav.setRoot(HomePage);
  }

  // go to register page

  register() {
    // debugger
    if (!this.email || !this.password) {
      let alert = this.alertCtrl.create({
        message: 'Please provide email and password',
        buttons: ['OK']
      });
      return alert.present();
    }
    if (this.password != this.confirmPassword) {
      let alert = this.alertCtrl.create({
        message: 'password and confirm password must be same',
        buttons: ['OK']
      });
      return alert.present();
    }



    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });
    loading.present();


    this.authService.register(this.email, this.password).subscribe(authData => {
      loading.dismiss();
      let json = {
        'partnerEmail': this.email,
        'uid': localStorage.getItem('uid')
      };
      this.profile.registerUser(json);
      this.nav.setRoot(RegisterPage, { 'auth': authData, 'email': this.email })
      /*this.profile.registerUser(json).then(
          ref=> {
              console.log(ref)
             // this.nav.setRoot(RegisterPage, {'auth':authData,'email':this.email})
          }
      )*/
      // this.nav.setRoot(RegisterPage, {'auth':authData,'email':this.email});
    }, error => {
      loading.dismiss();
      let alert = this.alertCtrl.create({
        message: error.message,
        buttons: ['OK']
      });
      alert.present();
    });
  }

  forgotPassword() {
    if (!this.email) {
      let alert = this.alertCtrl.create({
        message: 'Please provide the email address',
        buttons: ['OK']
      });
      return alert.present();
    }

    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });
    loading.present();

    this.authService.resetPassword(this.email).then(res => {
      loading.dismiss();
      this.nav.setRoot(LoginPage);
    }).catch(err => {
      loading.dismiss();
      let alert = this.alertCtrl.create({
        message: err.message,
        buttons: ['OK']
      });
      alert.present();
    });

  }

  loginwithgoogle(token) {
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });

    loading.present();
    var self = this;
    this.authService.loginWithSocialNetwork(token).then(function (data) {
      if (data) {
        console.log('Got token from firebase');
        let uid = '';
        let email = '';
        let photo = '';
        let name = '';
        let phoneNumber = '';
        if (self.platform.is('cordova') && token == 'google') {
          uid = data.uid;
          email = data.email;
          photo = data.photoURL;
          name = data.displayName;
          phoneNumber = data.phoneNumber;
        } else if (self.platform.is('cordova') && token == 'facebook') {

        } else {
          uid = data.user.uid;
          email = data.user.email;
          photo = data.user.photoURL;
          name = data.user.displayName;
          phoneNumber = data.user.phoneNumber;
        }

        localStorage.setItem('uid', uid);
        self.profile.getProfileDataKeys(uid).subscribe(function (user) {
          if (user.length > 0) {
            console.log('Send to Home page');
            self.nav.setRoot(HomePage);
          } else {
            console.log('Send to Register page');
            // let json = {
            //     'partnerEmail': email,
            //     'uid': localStorage.getItem('uid')
            // };
            // self.profile.registerUser(json);
            self.nav.setRoot(RegisterPage, { 'email': email, 'photo': photo, 'name': name, 'phoneNumber': phoneNumber, 'socialLogin': true });
          }
          loading.dismiss();
        });
      } else {
        console.log('Failed to get token from firebase');
        alert('Login Failed / Cancelled');
      }
    }).catch(err => {
      console.log('Failed to connect to Firebase - ' + err);
      loading.dismiss();
    });
  }

}



