import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AngularFireDatabase } from "angularfire2/database";
/*
  Generated class for the ProfileProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ProfileProvider {

  constructor(public db: AngularFireDatabase) {
    
  }

  getProfileData(uid) {
    return this.db.list('/customer', ref => ref.orderByChild('uid').equalTo(uid)).valueChanges();
  }
  getPartnerProfile(id) {
    return this.db.list('/partner', ref => ref.orderByChild('uid').equalTo(id)).valueChanges();
  }
  getProfileDataKeys(uid) {
    return this.db.list('/customer', ref => ref.orderByChild('uid').equalTo(uid)).snapshotChanges();
  }

  getProfileServices(uid) {
    return this.db.list('/service', ref => ref.orderByChild('puid').equalTo(uid)).valueChanges();
  }

  getProfileServicesKeys(uid) {
    return this.db.list('/service', ref => ref.orderByChild('puid').equalTo(uid)).snapshotChanges();
  }

  registerUser(json) {

    var partner_profile = {
      'partnerEmail': json.partnerEmail,
      'uid': json.uid
    };

    let data = this.db.list('/customer');
    data.push(partner_profile);
  }


  saveProfile(json) {

    if (!json.address) {
      json.address = [];
    }
    if (!json.createdAt) {
      json.createdAt = '';
    }
    if (!json.partnerEmail) {
      json.partnerEmail = '';
    }
    if (!json.partnerGender) {
      json.partnerGender = '';
    }
    if (!json.idCard) {
      json.idCard = ''
    }
    if (!json.partnerFirstName) {
      json.partnerFirstName = ''
    }
    if (!json.partnerLastName) {
      json.partnerLastName = '';
    }
    if (!json.profilePicture) {
      json.profilePicture = '';
    }
    if (!json.partnerPhone) {
      json.partnerPhone = '';
    }
      if (!json.partnerFromTime) {
          json.partnerFromTime = '';
      }
      if (!json.partnerToTime) {
          json.partnerToTime = '';
      }
      if (!json.partnerBio) {
          json.partnerBio = '';
      }

    var partner_profile = {
      'addresses': json.address,
      'createdAt': json.createdAt,
      'partnerEmail': json.partnerEmail,
      'partnerGender': json.partnerGender,
      'partnerFirstName': json.partnerFirstName,
      'partnerLastName': json.partnerLastName,
      'profilePicture': json.profilePicture,
      'partnerPhone': json.partnerPhone,
        'partnerFromTime': json.partnerFromTime,
        'partnerToTime': json.partnerToTime,
        'partnerBio': json.partnerBio,
      'uid': json.cuid
    };

    return this.db.list('/customer').push(partner_profile);
  }

  updateProfile(arr1, json) {
    
    if (!json.address) {
      json.address = [];
    }
    if (!json.createdAt) {
      json.createdAt = '';
    }
    if (!json.partnerEmail) {
      json.partnerEmail = '';
    }
    if (!json.partnerGender) {
      json.partnerGender = '';
    }
    if (!json.idCard) {
      json.idCard = ''
    }
    if (!json.partnerFirstName) {
      json.partnerFirstName = ''
    }
    if (!json.partnerLastName) {
      json.partnerLastName = '';
    }
    if (!json.profilePicture) {
      json.profilePicture = '';
    }
    if (!json.partnerPhone) {
      json.partnerPhone = '';
    }
      if (!json.partnerFromTime) {
          json.partnerFromTime = '';
      }
      if (!json.partnerToTime) {
          json.partnerToTime = '';
      }
      if (!json.partnerBio) {
          json.partnerBio = '';
      }

    var partner_profile = {
      'addresses': json.address,
      'createdAt': json.createdAt,
      'partnerEmail': json.partnerEmail,
      'partnerGender': json.partnerGender,
      'partnerFirstName': json.partnerFirstName,
      'partnerLastName': json.partnerLastName,
      'profilePicture': json.profilePicture,
      'partnerPhone': json.partnerPhone,
        'partnerFromTime': json.partnerFromTime,
        'partnerToTime': json.partnerToTime,
        'partnerBio': json.partnerBio,
      'uid': json.cuid
    };

    return this.db.list('/customer').update(arr1[0].key, partner_profile);
  }

}



