import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams ,LoadingController} from 'ionic-angular';
import { LoginPage } from '../login/login';
import { MenuController } from 'ionic-angular/components/app/menu-controller';
import {RegisterPage} from '../register/register';
import {BookingsPage} from "../bookings/bookings";
import {AuthProvider} from "../../providers/auth/auth";
import { HomePage } from '../home/home';
/**
 * Generated class for the StartPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-start',
    templateUrl: 'start.html',
})
export class StartPage {

    constructor(public authService: AuthProvider,
                public loadingCtrl: LoadingController,public navCtrl: NavController, public navParams: NavParams,
                public menu: MenuController) {
        this.menu.enable(false);
        if(localStorage.getItem('uid')!==null){
            if(localStorage.getItem('email')!==null){
                let loading = this.loadingCtrl.create({
                    content: 'Please wait while we are signing you back in.'
                });
                loading.present();

                this.authService.login(localStorage.getItem('email'), localStorage.getItem('password')).then(authData => {
                    loading.dismiss();
                   // this.navCtrl.setRoot(BookingsPage);
                     this.navCtrl.setRoot(HomePage);
                });
            }

        }
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad StartPage');
    }

    start() {

        this.navCtrl.setRoot(LoginPage);

    }
}



