import {Component} from "@angular/core";
import {AlertController, IonicPage, LoadingController, NavController, NavParams} from "ionic-angular";
import {NotificationsProvider} from "../../providers/notifications/notifications";
import {AuthProvider} from "../../providers/auth/auth";
import {MenuController} from "ionic-angular/components/app/menu-controller";
/**
 * Generated class for the NotificationsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-notifications',
    templateUrl: 'notifications.html',
})
export class NotificationsPage {

    notificationsRef: any = [];
    notifications: any = [];

    constructor(public navCtrl: NavController,
                public navParams: NavParams,
                public notificationService: NotificationsProvider,
                public loadingCtrl: LoadingController,
                public alertCtrl:AlertController,
                private _authService: AuthProvider,
    public menu: MenuController
) {

    this.menu.enable(true);


    let loading = this.loadingCtrl.create({
            content: 'Loading notifications Please Wait...'
        });
        loading.present();
        this.notificationsRef = this.notificationService
            .getInAppNotificationsByUid(this._authService.getUserData().uid);
        this.notifications = this.notificationsRef.snapshotChanges().map(changes => {
            return changes.map(c => ({key: c.payload.key, ...c.payload.val()}));
        });
        loading.dismiss();
    }

    removeNotification(notificationKey: any) {
        let loading = this.loadingCtrl.create({
            content: 'Loading notifications Please Wait...'
        });
        this.notificationsRef.remove(notificationKey)
        loading.present();
        this.notificationsRef = this.notificationService
            .getInAppNotificationsByUid(this._authService.getUserData().uid);
        this.notifications = this.notificationsRef.snapshotChanges().map(changes => {
            return changes.map(c => ({key: c.payload.key, ...c.payload.val()}));
        });
        loading.dismiss();

    }

    presentConfirm(notification) {
        let alert = this.alertCtrl.create({
          title: notification.title,
          message: notification.description,
          buttons: [
            {
              text: 'Cancel',
              role: 'cancel',
              handler: () => {
                console.log('Cancel clicked');
              }
            },
            {
              text: 'Remove',
              handler: () => {
                  console.log('Remove clicked');
                this.removeNotification(notification.key);
              }
            }
          ]
        });
        alert.present();
      }

}



