  register() {
    let loading = this.loadingCtrl.create({
      content: 'please wait'
    });

    loading.present();

    var d = new Date();
    var n = d.toISOString();
    let json = this.model;
    json.services = this.services;
    json.address = this.addresses;
    json.profilePicture = this.profile_pic;
    json.partnerPhone = this.model.partnerPhone;

    json.idCard = {
      'name': this.file_name,
      'picture': this.file_src
    }
    if (this.file_src == '' || this.file_src == ' ' || !(this.file_src)) {
      delete json.idCard;
    }
    json.createdAt = n;
    json.cuid = localStorage.getItem('uid');

    this.flag = false;

    let arrayOfKeys = Object.keys(this.model);
    let error = false;

    /* for(let obj of arrayOfKeys){
        if(!(this.model[obj])){
             error=true;
             this.loadError("please provide "+obj);
            if(loading){
                loading.dismiss();
                loading=null;
            }
             break;
        }
     }*/
    // if(!error){
    if (1) {
      // if(this.file_src=='' || this.file_src==' ' || !(this.file_src)){
      if (0) {
        this.loadError("please provide id proof");
        if (loading) {
          loading.dismiss();
          loading = null;
        }
      } else {
        if (this.navParams.get('socialLogin')) {
          this.profileService.saveProfile(json).then(resolve => {
            if (loading) {
              loading.dismiss();
              loading = null;
            }
          }, reject => {
            console.log('error while storing data. please try again');
            if (loading) {
              loading.dismiss();
              loading = null;
              this.loadError('Something went wrong. Try again');
            }
          });
        } else {
          this.profileService.updateProfile(this.profileKeys, json).then(resolve => {
            if (loading) {
              loading.dismiss();
              loading = null;

            }
            this.loadSuccess();
          }, reject => {
            console.log('error while storing data. please try again');
            if (loading) {
              loading.dismiss();
              loading = null;
              this.loadError('Something went wrong. Try again');
            }
          });
        }
      }
    }
  }

