import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AngularFireAuth } from "angularfire2/auth";
import * as firebase from 'firebase/app';
import { AngularFireDatabase } from "angularfire2/database";
import { Observable } from "rxjs/Observable";
import { GooglePlus } from '@ionic-native/google-plus';
import { Platform } from 'ionic-angular';
import { Facebook } from '@ionic-native/facebook'

/*
  Generated class for the AuthProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class AuthProvider {

  constructor(public afAuth: AngularFireAuth, public db: AngularFireDatabase, public googleplus: GooglePlus, public platform: Platform, public facebook: Facebook) {

  }

  // get current user data from firebase
  getUserData() {
    return this.afAuth.auth.currentUser;
  }

  login(email, password) {
    return this.afAuth.auth.signInWithEmailAndPassword(email, password);
  }

  logout() {
    return this.afAuth.auth.signOut();
  }

  register(email, password) {
    return Observable.create(observer => {
      this.afAuth.auth.createUserWithEmailAndPassword(email, password).then((authData: any) => {
        // set up initial data
        localStorage.setItem('uid', authData.uid);
        this.updateUserProfile(authData);
        observer.next();
      }).catch((error: any) => {
        if (error) {
          observer.error(error);
        }
      });
    });
  }

  // update user display name and photo
  updateUserProfile(user) {
    let name = user.name ? user.name : user.email;
    let photoUrl = user.photoURL;

    this.getUserData().updateProfile({
      displayName: name,
      photoURL: photoUrl
    });

    // create or update passenger
    this.db.object('partners/' + user.uid).update({
      name: name,
      photoURL: photoUrl,
      email: user.email,
    })
  }

  resetPassword(email: string) {
    return this.afAuth.auth.sendPasswordResetEmail(email);
  }

  signoutUser() {
    return this.afAuth.auth.signOut();
  }

  loginWithSocialNetwork(token): Promise<any> {
    return new Promise((resolve, reject) => {
      let clientid = '603375740986-41g69md2laq28lfotvvtk3n7qeof3kdj.apps.googleusercontent.com';
      let config = {
        'webClientId': clientid,
        'offline': false
      };

      if (token == 'google') {
        if (this.platform.is('cordova')) {
          console.log('Trying to connect to social site for auth key');
          this.googleplus.login(config).then((res) => {
            const firecreds = firebase.auth.GoogleAuthProvider.credential(res.idToken);
            resolve(this.afAuth.auth.signInWithCredential(firecreds));
          }).catch((err) => {
            console.log('Google Error - ' + err);
            reject(err);
          });
        } else {
          resolve(this.afAuth.auth.signInWithPopup(new firebase.auth.GoogleAuthProvider));
        }
      } else {
        if (this.platform.is('cordova')) {
          this.facebook.login(['email']).then(response => {
              const facebookCredential = firebase.auth.FacebookAuthProvider.credential(response.authResponse.accessToken);
              resolve(this.afAuth.auth.signInWithCredential(facebookCredential));
            }).catch((error) => {
              console.log('Facebook Error - ' + JSON.stringify(error));
              reject(error);
            });
        } else {
          resolve(this.afAuth.auth.signInWithPopup(new firebase.auth.FacebookAuthProvider));
        }
      }
    });
  }

}



